import{useKeepAwake}from'expo-keep-awake';import{useIntl}from'react-intl';import{ETranslations}from'@onekeyhq/shared/src/locale';import{useAppExitPrevent,useExtensionUpdatingFromExpandTab,useModalExitPrevent}from"../hooks/useFirmwareUpdateHooks";export function ForceExtensionUpdatingFromExpandTab(){useExtensionUpdatingFromExpandTab();return null;}export function FirmwareUpdateExitPrevent({shouldPreventRemove=true}){var intl=useIntl();var title=intl.formatMessage({id:ETranslations.update_quit_update});var message=intl.formatMessage({id:ETranslations.update_quit_update_desc});useKeepAwake();useModalExitPrevent({shouldPreventRemove,title,message});useAppExitPrevent({title,message,shouldPreventExitOnAndroid:false});return null;}